import React from 'react';

export default function() {
    return <>
        <p>Today’s exercise:</p>
        <p className='emphasis'>
            Estimate the specific heat capacity of copper (J/g °C).
        </p>
        <ul>
        <li>References allowed: None.</li>
        <li>Tools allowed: Scientific calculator.</li>
        </ul>

        <h2>Crude Approximation</h2>
        <p>
            First, I expect the heat capacity to be fairly close to \(3 k_B\) per atom, by the equipartition theorem.
            This corresponds to a molar heat capacity of \(3R\),
            and from memory I recall that \(R = 0.0821\) J/mol K.
            I know copper has the atomic number \(Z = 29\) but have no idea how many neutrons it has.
            A priori it probably has \(29\) neutrons as well, so its atomic weight should be around \(58\) g/mol.
            This yields an estimate of {String.raw`\(4.25 \times 10^{-3}\)`} J/gK for the specific heat capacity.
        </p>
        <p>Time used: 6 minutes 45 seconds</p>

        <h2>Debye Model</h2>
        <p>
            I expect the Debye model to be a good enough approximation for this question.
            Of course I don’t recall the results, so I’ll have to rederive them.
        </p>
        <p>
            Consider a cube of copper with sidelength \(L\).
            There are phonon modes with wavevectors \(k\) whose components are quantized in units of \(2\pi/L\).
            Phonons are bosons, so they will follow Bose statistics to yield the total energy
            {String.raw`\[
                E = \frac{\hbar^2}{2m} \int_0^{\pi/a} \frac{\mathrm{d}^3 k}{(2\pi)^3} \, \frac{k^2}{e^{\beta \hbar^2 k^2/2m} - 1}
            \]`}
        </p>
    </>;
}
